<template>
    <div>
        <h3>{{ getTitle }}</h3>
        <p>{{ getMemo }}</p>
    </div>
</template>

<script>
export default {
    name: 'PageView',
    computed: {
        getTitle: function() {
            return this.$store.state.board[this.$route.params.id-1].title;
        },
        getMemo: function() {
            return this.$store.state.board[this.$route.params.id-1].memo;
        }
    }
}
</script>